/**
 * WordPress dependencies
 */
import { useState, createContext, useContext } from '@wordpress/element';

const StateContext = createContext();

const flatten = ( obj ) => {
	var out = {};

	for ( var i in obj ) {
		if ( ! obj.hasOwnProperty( i ) ) continue;

		if ( typeof obj[ i ] == 'object' && obj[ i ] !== null ) {
			var flatObject = flattenObject( obj[ i ] );
			for ( var x in flatObject ) {
				if ( ! flatObject.hasOwnProperty( x ) ) continue;

				out[ i + '.' + x ] = flatObject[ x ];
			}
		} else {
			out[ i ] = obj[ i ];
		}
	}
	return out;
};

export function DataTabProvider( { children } ) {
	const [ action, setAction ] = useState( 'select' );
	const [ activeStore, setActiveStore ] = useState( null );
	const [ activeFunction, setActiveFunction ] = useState( null );

	const stores = {};

	for ( let i = 0; i < Object.keys( wp ).length; i++ ) {
		const key = Object.keys( wp )[ i ];
		if ( wp[ key ].store ) {
			stores[ key ] = wp[ key ];
		}
	}

	const allData = [];

	const search = ( keyword ) => {
		const matches = [];

		// Go 2 levels
		for ( let j = 0; j < Object.keys( wp ).length; j++ ) {
			const key = Object.keys( wp )[ j ];
			allData.push( key );

			if ( key.indexOf( keyword.toLowerCase() ) >= 0 ) {
				matches.push( Object.keys( wp ) );
			}

			for ( let k = 0; k < OBject.keys( wp[ key ] ).length; k++ ) {
				const secondKey = wp[ key ][ k ];

				if ( secondKey ) {
					allData.push( secondKey );
				}
			}
		}
	};

	return (
		<StateContext.Provider
			value={ {
				action,
				setAction,
				activeStore,
				setActiveStore,
				activeFunction,
				setActiveFunction,
				stores,
				search,
			} }
		>
			{ children }
		</StateContext.Provider>
	);
}

export function useDataTab() {
	const context = useContext( StateContext );

	if ( context === undefined ) {
		throw new Error( 'useData must be used within a Provider' );
	}

	return context;
}
